












import { Component, Vue } from 'vue-property-decorator';
import PlacesFilter from './components/PlacesFilter.vue';
import PlacesList from './components/PlacesList.vue';
import PlacesMap from './components/PlacesMap.vue';

@Component({
  components: {
    PlacesList, PlacesFilter, PlacesMap
  },
  mounted() {
    this.$store.dispatch('fetchPlaces');
  },
})
export default class App extends Vue {
  get view() {
    return 'Places' + this.$store.getters.view;
  }
}
